<template>
  <div ref="mescroll" class="mescroll">
    <div>
      <slot></slot>
    </div>
  </div>
</template>

<script>
// mescroll.min.jsmescroll.min.css
import MeScroll from 'mescroll.js'
import 'mescroll.js/mescroll.min.css'

export default {
  name: 'MeScrollVue',
  data: function () {
    return {
      mescroll: null,
      lastScrollTop: 0, // 
      lastBounce: null // ios
    }
  },
  props: {
    up: Object,
    down: Object
  },
  mounted: function () {
    this.mescroll = new MeScroll(this.$refs.mescroll, {
      up: this.up,
      down: this.down
    })
    this.$emit('init', this.mescroll) // initmescroll
  },
  methods: {
    beforeRouteEnter () {
      if (this.mescroll) {
        // 
        if (this.lastScrollTop) {
          this.mescroll.setScrollTop(this.lastScrollTop)
          setTimeout(() => { // ,setScrollToponScroll,
            this.mescroll.setTopBtnFadeDuration(0) // 
          }, 16)
        }
        // isBounce
        if (this.lastBounce != null) this.mescroll.setBounce(this.lastBounce)
      }
    },
    beforeRouteLeave () {
      if (this.mescroll) {
        this.lastScrollTop = this.mescroll.getScrollTop() // 
        this.mescroll.hideTopBtn(0) // ,
        this.lastBounce = this.mescroll.optUp.isBounce // ios
        this.mescroll.setBounce(true) // bounce
      }
    }
  }
}
</script>

